body { font-family: sans-serif; }

.carousel {
  background: none;
}

.carousel-cell {
  width: 95%;
  height: 400px;
  margin-right: 10px;
}

.carousel-cell-image {
  display: block;
  max-height: 95%;
  margin: 0 auto;
  max-width: 95%;
  opacity: 0;
  -webkit-transition: opacity 0.4s;
          transition: opacity 0.4s;
}

/* fade in lazy loaded image */
.carousel-cell-image.flickity-lazyloaded,
.carousel-cell-image.flickity-lazyerror {
  opacity: 1;
}




/* Flexbox-Container-Settings */
.gallery-cell {
  width: 100%;
  overflow-y: auto;
  // Flexbox Settings
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: stretch;
  align-items: center;
}
.gallery-cell > div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  overflow: hidden;

}

.gallery-cell img {
  height: 500px; /* Inherit the height from the parent div */
}

.gallery-cell p {
  margin: 0.5em auto 0.5em auto;
  max-width: 50em;
  text-align:center;
  margin-bottom:0px
} 

// Page Dots
.flickity-page-dots {
  bottom: 0px;
}
.flickity-page-dots .dot {
  width: 3%;
  min-width: 5px;
  height: 15px;
  opacity: 1;
  background: transparent;
  border-top: 3px solid #fff;
  opacity: 0.3;
  border-radius: 0;
  margin: 0;
}
.flickity-page-dots .dot.is-selected {
  opacity: 1;
}

